<template>
  <div id="app" class="app-viewport">
    <v-tabs icons-and-text centered dark color="cyan">
      <v-tabs-slider color="yellow" style="width:100%;"></v-tabs-slider>
      <v-tab to="/">
        Matchs
        <v-icon>access_time</v-icon>
      </v-tab>
      <v-tab to="/classements">
        Classement
        <v-icon>start</v-icon>
      </v-tab>
      <v-tab to="/commentaires">
        Commentaires
        <v-icon>comment</v-icon>
      </v-tab>
    </v-tabs>

    <div class="md-layout" style="background-color:#FFF59D" id="connexionBar" ref="connexionBar" v-show="connexionDown">
      <div class="md-layout-item"><md-icon class="md-primary">warning</md-icon></div>
      <div class="md-layout-item" md-vertical-align="center">Vous êtes hors connexion ...</div>
    </div>

    <div class="md-layout" style="background-color:#FFF59D" id="serverBar" ref="serverBar" v-show="serverDown">
      <div class="md-layout-item"><md-icon class="md-primary">warning</md-icon></div>
      <div class="md-layout-item" md-vertical-align="center">Serveur injoignable ...</div>
    </div>
    <div class="simple">
      <md-button class="md-raised md-accent" @click.native="testPush">V 9h37 23 02</md-button>
    </div>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
let connexionDown = false
let serverDown = false

export default {
  name: 'app',

  methods: {
    showConnexionBar (event) {
      this.$localStorage.set('online', false)
      this.connexionDown = true
      window.scrollTo(0, 0)
    },
    hideConnexionBar (event) {
      this.$localStorage.set('online', true)
      this.connexionDown = false
    },
    showServerBar () {
      this.serverDown = true
      window.scrollTo(0, 0)
    },
    hideServerBar () {
      this.serverDown = false
    },
    testPush: function () {
      Notification.requestPermission(function (result) {
        if (result === 'granted') {
          console.log('testPush in App.vue')
          navigator.serviceWorker.ready.then(function (registration) {
            registration.showNotification('LFP', {
              lang: 'fr',
              body: 'Test push...',
              tag: 'IDLFPDemo',
              icon: '../static/img/icons/android-chrome-200x200_LFP.png'
            })
          })
        }
      })
    }
  },
  data () {
    return {
      connexionDown,
      serverDown
    }
  },

  mounted () {
    console.log('app.vue mounted')
    window.addEventListener('online', this.hideConnexionBar)
    window.addEventListener('offline', this.showConnexionBar)
    if (!navigator.onLine) this.showConnexionBar()
  },
  watch: {
    notif: function () {
      self.addEventListener('notificationclick', event => {
        const notification = event.notification
        const action = event.action
        const link = notification.data.link
        if (action !== 'close') {
          if (link) {
            this.openWindow(link)
          }
        }
        notification.close()
        console.log('notificationclick action is', action)
      })
    }
  }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  margin-top: 10px;
}

.complete-example {
  height: 540px;
  display: flex;
  flex-flow: column;
  position: relative;
  overflow: hidden;
  z-index: 1;

  .main {
    overflow: auto;
  }
}

h2 {
  padding-left: 50px;
}

md-toolbar>md-tabs{
  height: auto;
  width: auto;
}
.md-tabs + .md-tabs {
    margin-top: 24px;
}
</style>



